.quiz {
    height: 500px;
    width: 800px;
    background-color: var(--sidebar-background-color-main);
    border-radius: 15px;
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    &::after {
        content: '';
        position: absolute;
        display: block;
        top: 6px;
        left: 2.5%;
        width: 95%;
        height: 100%;
        background-color: var(--sidebar-background-color-main);
        filter: opacity(.8);
        border-radius: 15px;
        z-index: -1;
    }
    &::before {
        content: '';
        position: absolute;
        display: block;
        top: 12px;
        left: 5%;
        width: 90%;
        height: 100%;
        background-color: var(--sidebar-background-color-main);
        filter: opacity(.3);
        border-radius: 15px;
        z-index: -2;
    }
        .questions-progressbar {
            width: 80%;
            height: 10px;
            margin-top: 50px;
            background: lightgray;
            border-radius: 5px;
            position: relative;
    
            &-filler {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                background: linear-gradient(90deg, rgba(31, 255, 236, 1) 0%, rgba(114, 189, 255, 1) 56%, rgba(0, 97, 187, 1) 100%);
                border-radius: 5px;
                transition: all .5s ease-in-out;
            }
        }
    .questions {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        width: 100%;
        height: 100%;
        
        &-next-button{
            width: 150px;
                height: 50px;
                border-radius: 5px;
                border: 1px solid black;
                background: none;
                margin: 0;
                transition: all .15s ease-in-out;
            
                &:hover {
                    cursor: pointer;
                    background: var(--accent-color);
                    color: white;
                    transform: scale(1.05);
                }
        }
    }
    &-question {
        &-text {
            font-size: 1.2rem;
            text-align: center;
            max-width: 80%;
            font-weight: 600;
            margin: 0 auto;
        }
    }
    &-answer-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0;
        padding: 0;
        .quiz-answer {
            list-style-type: none;
            width: 100%;
            padding: 10px;
            margin: 5px;
            font-size: .7rem;
            border: 1px solid black;
            border-radius: 5px;
            transition: all .15s ease-in-out;
            &:hover {
                    cursor: pointer;
                    background: var(--accent-color);
                    color: white;
                    transform: scale(1.02);
                }
            &.correct{
                background: rgba(0, 128, 0, .9);
                color: white;
                border: none;
            }
            &.incorrect{
                background: rgba(255, 0, 0, .9);
                color: white;
                border: none;
            }
            
            
        }
    }
    .result {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        width: 100%;
        height: 100%;
        &-congr {
            font-size: 1.2rem;
            margin: 0;
        }
        &-text {
            font-size: .7rem;
            margin: 0;
        }
        &-image {
            width: 150px;
            height: 150px;
            border-radius: 50%;
        }
        &-button {
            width: 150px;
            height: 50px;
            border-radius: 5px;
            border: 1px solid black;
            background: none;
            margin: 0;
            transition: all .15s ease-in-out;
            &:hover {
                cursor: pointer;
                background: var(--accent-color);
                color: white;
                transform: scale(1.05);
            }
        }
    }
}